import returns from '../../common/returns.vue'
export default {
	components: {
		returns
	},
	data() {
		return {
			title: '拼团订单详情',
			data: '',
			data_list: '',
			pay_list: '',
			show: false,
			payment_id: '',
			address:'',
			payment_name: '',
			teamid:''
		}
	},
	methods: {
		jump(url) {
			uni.navigateTo({
				url: url
			})
		},
		choice(index) {
			for (let s of this.pay_list) {
				s.choice = false
			}
			this.pay_list[index].choice = true
			this.pay_list = JSON.parse(JSON.stringify(this.pay_list))
			this.payment_id = this.pay_list[index].id
			this.payment_name = this.pay_list[index].payment
		},
		cancel_return(id){//取消退款申请
			let that = this
			console.log(id)
			return
			uni.showModal({
			    title: '提示',
			    content: '是否取消退款/退货申请？',
			    success: function (res) {
			        if (res.confirm) {
			            that.service.entire(that,'get',that.APIconfig.api_root.subuser.threeuser.g_AftersaleCancel,{
							id:id,
							user_id:that.$store.state.user.id,
						},function(self,res){
							uni.showToast({
								icon:'none',
								title:res.msg
							})
			            	if(res.code == 0){
								setTimeout(function(){
									self.service.returns()
								},1000)
							}
			            })
			        } else if (res.cancel) {
			           return
			        }
			    }
			});
		},
		determine(){//确定收货
			let that = this
			uni.showModal({
			    title: '提示',
			    content: '是否确定收货？',
			    success: function (res) {
			        if (res.confirm) {
			            that.service.entire(that,'get',that.APIconfig.api_root.subuser.threeuser.g_Collect,{
							id:that.data.id,
							
						},function(self,res){
							uni.showToast({
								icon:'none',
								title:res.msg
							})
			            	if(res.code == 0){
								setTimeout(function(){
									self.service.returns()
								},1500)
							}
			            })
			        } else if (res.cancel) {
			           return
			        }
			    }
			});
		},
		payment() {
			//提交
			let that = this
			if (!this.payment_id) {
				uni.showToast({
					icon: 'none',
					title: '请选择支付方式'
				})
				return
			}
			uni.showModal({
			    title: '提示',
			    content: '是否确定支付？',
			    success: function (res) {
			        if (res.confirm) {
			            that.service.entire(that, 'get', that.APIconfig.api_root.subhome.threehome.g_pay, {
			            	user_id:that.$store.state.user.id,
			            	orderid: that.data.id,
			            	payment_id: that.payment_id,
			            }, function(self, ref) {
			            	self.service.order(ref,self,'../assemble_order','pages/subuser/assemble_order')
			            })
			        } else if (res.cancel) {
			           return
			        }
			    }
			});
			

		}
	},
	onLoad(options) {
		this.teamid = options.temid
		this.service.entire(this, 'get', this.APIconfig.api_root.subuser.threeuser.g_OrderDetail, {
			orderid: options.id,
			teamid:options.temid,
			user_id:this.$store.state.user.id,
		}, function(self, res) {
			self.data = res.data.order
			self.data_list = res.data.goods
			self.address = res.data.address
			let data = res.data.payment_list
			for (let s of data) {
				s.choice = false
			}
			console.log(data)
			self.pay_list = data
		})
	}
}
